import './globals.css';
import { StoreProvider } from '@/store';
import { InstallProvider } from '@/contexts/InstallContext';
import { InstallPrompt } from '@/components/InstallPrompt';

export const metadata = {
    title: 'Agende + | Gestão Inteligente',
    description: 'Sistema de Agendamento e Gestão para Empresas',
    manifest: '/manifest.json',
};

export default function RootLayout({
    children,
}: {
    children: React.ReactNode
}) {
    return (
        <html lang="pt-BR" className="h-full overflow-hidden">
            <body className="bg-gray-50 text-gray-900 antialiased h-full overflow-hidden">
                <StoreProvider>
                    <InstallProvider>
                        {children}
                        <InstallPrompt />
                    </InstallProvider>
                </StoreProvider>
            </body>
        </html>
    );
}
